@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  font-size: 62.5%;
}

.flexDisplay, .price_b, .list, .head {
  display: flex;
  justify-content: center;
  align-items: center;
}

.card {
  width: 20rem;
  height: 30rem;
  font-family: "Roboto", sans-serif;
  margin-right: 5rem;
  position: relative;
  border-radius: 10px;
  background-color: transparent;
  transform-style: preserve-3d;
  float: left;
}
.card:hover div[class=front-face] {
  transform: rotateY(-180deg);
  box-shadow: -2rem 0.7rem 0.3rem 0.1rem #6c757d;
}
.card:hover div[class=back-face] {
  transform: rotateY(0);
  box-shadow: 2rem 0.7rem 0.3rem 0.1rem #6c757d;
}

.container {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.front-face {
  position: absolute;
  top: 0;
  left: 0;
  width: 20rem;
  height: 30rem;
  overflow: hidden;
  transition: transform 1s ease-in-out, box-shadow 0.5s ease-in-out;
  transform: rotateY(0);
  border: 1px solid #6c757d;
  border-radius: 10px;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  box-shadow: 2rem 0.7rem 0.3rem 0.1rem #6c757d;
}

.back-face {
  position: absolute;
  top: 0;
  left: 0;
  width: 20rem;
  height: 30rem;
  transform: rotateY(180deg);
  transition: transform 1s ease-in-out, box-shadow 0.5s ease-in-out;
  border-radius: 10px;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  border: 1px solid #6c757d;
  box-shadow: -2rem 0.7rem 0.3rem 0.1rem #6c757d;
}

.head {
  font-size: 2.2rem;
  margin: 1rem 0rem 1rem 0rem;
}

.list {
  align-items: start;
  flex-direction: column;
  list-style: none;
  font-weight: 700;
  font-size: 1.2rem;
  padding: 0rem 0rem 2rem 1.5rem;
}
.list li {
  width: 96%;
  padding-bottom: 0.5rem;
  margin-bottom: 1.5rem;
  border-bottom: 1px solid #f5f5f5;
}
.list li:last-child {
  border-bottom: 0px solid #f5f5f5;
}

.btn_b {
  display: block;
  margin: 0 auto;
  border: 0;
  background: #007bff;
  cursor: pointer;
  font-weight: bold;
  font-family: inherit;
  border-radius: 1rem;
  padding: 1rem 1rem;
  font-size: 1rem;
  transition: all 0.3s;
}
.btn_b:focus {
  outline: 0;
}
.btn_b:hover {
  background-color: #0062cc;
}
.btn_b:active {
  background-color: #0062cc;
  transform: scale(0.9);
}

.price_b {
  padding: 0;
  margin: 1rem 0 0.2rem 0;
  font-size: 1.5rem;
  font-weight: bold;
}

